.container {
  background-color: #1e90ff;
  justify-content: center;
  align-items: center;
  padding: 20px 30px;
  display: flex;
}

.gallery {
  max-width: 1158px;
  flex-flow: wrap;
  gap: 10px;
  margin: 10px auto;
  display: flex;
}

.photo-card {
  width: calc(25% - 7.5px);
}

.hidden {
  display: none;
}

.info {
  border: 1px solid #dcdcdc;
  border-top: none;
  flex-direction: column;
  gap: 8px;
  padding: 16px;
  display: flex;
}

.search-form input {
  border-radius: 8px;
  padding: 8px;
  font-size: 15px;
}

.search-button {
  color: #fff;
  background-color: #d2691e;
  border-radius: 8px;
  padding: 8px 20px;
  font-size: 15px;
}

